
* {
    margin: 0;
    padding: 0;
}
body {
    margin: 0;
    font: 9pt/1.5em Arial,sans-serif;
    letter-spacing: 0;
    line-height: 18px;
}
html, body {
    margin: 0;
    padding: 0;
    height: 100%;
}
div.block {
    position: relative;
}
table {
    width: 99%;
}
table {
    border-collapse: collapse;
}
tbody {
    border-top: 1px solid #ccc;
}
tr.odd {
    background-color: white;
}
tr.even, tr.odd {
    background-color: #eee;
    border-bottom: 1px solid #ccc;
    padding: .1em .6em;
}
tr.even td, tr.odd td {
    padding: 10px 5px;
}
tr.even td, tr.odd td {
    padding: 10px 5px;
}
#page {
    margin: 0 auto;
    min-height: 100%;
    height: auto !important;
    height: 100%;
}
#page, #footer {
    position: relative;
}
.page-title-layer h1 {
    margin-top: 5px;
    margin-bottom: 5px;
}
h1.title {
    color: #333;
    font-size: 14pt;
    font-weight: bold;
    padding-bottom: 5px;
}
h1 {
    font-size: 2.2em;
    font-weight: 300;
    padding: 0 0 .5em;
    margin: 0;
}
h1, h2, h3, h4, h5, h6 {
    line-height: 1.3em;
}
.container-inline div, .container-inline label {
    display: inline;
}
#main-wrapper {
    width: 980px;
    margin: 0 auto;
}
#content-wrapper {
    clear: both;
}
.two-sidebars .center, .sidebar-first .center {
    margin-left: 270px;
}
.two-sidebars .center, .sidebar-first .center {
    margin-left: 270px;
}
.inner {
    padding: 0;
}
#content {
    float: left;
    width: 100%;
    margin-right: -100%;
    padding: 0;
    padding-top: 0;
}
.inner {
    padding: 0;
}
.form-item, .form-actions {
    margin-top: 1em;
    margin-bottom: 1em;
}
form {
    margin: 0;
    padding: 0;
}
.region {
    padding-bottom: 3px;
}
p {
    margin: 0 0 1em 0;
}
a:visited {
    color: #369;
}
a {
    color: #00407f;
    text-decoration: underline;
}
img {
    border: 0;
}
.clearfix {
    display: block;
}
.clearfix:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}

#header {
    height: 86px;
    background: url(/static/img/kompis-header-bg.png) repeat-x;
}
#header, #footer, .mission, .breadcrumb, .node {
    clear: both;
}
#header-wrapper {
    width: 980px;
    margin: 0 auto;
    background-repeat: no-repeat;
    height: 80px;
    position: relative;
}
.header-phone-block-link {
    float: right;
    margin-right: 25px;
}
.header-phone-block-link a {
    color: #FFF;
}

.logo {
    width: 162px;
    height: 80px;
    float: left;
    left: 43px;
}

.block.main-menu {
    background-position: bottom;
    margin: 0;
    padding: 0;
    /* float: right; */
    background: url(/static/img/menu_top_bg_line.png) no-repeat;
    background-position: 2px 100%;
    position: absolute ;
    bottom: -6px;
    left: 265px;
}
.block.main-menu  ul.menu {
    margin: 0;
    padding: 0;
    list-style: none;
    margin-top: 1px;
    border: 0;
    text-align: left;
}
.block.main-menu  ul.menu li {
    margin: 0;
    padding: 0;
    list-style: none;
    float: left;
}
.block.main-menu ul.menu li a {
    font: bold 8pt Arial;
    color: #fff;
    text-decoration: none;
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 5px 5px 0 0;
    -webkit-border-radius: 5px 5px 0 0;
    /* behavior: url(//tdc.ua/sites/all/themes/basic/css/pie/PIE.htc); */
    margin-left: 2px;
    background: url(/static/img/menu-bg.png);
    background-repeat: repeat-x;
    height: 30px;
    display: block;
    margin-top: 6px;
    line-height: 30px !important;
    letter-spacing: normal;
    cursor: pointer;
}
.block.main-menu ul.menu li a:hover {
    background: url(/static/img/kompis-menu-bg-hover.png);
    color: #fff;
}
.block.main-menu ul.menu li a.router-link-active {
    background: url(/static/img/menu-bg-active.png);
    color: #000;
}
.block.main-menu  ul.menu li.first a {
    background: url(/static/img/domik.png);
    background-repeat: no-repeat;
    width: 26px;
    font-size: 0;
}
.block.main-menu ul.menu li.first a.router-link-exact-active,
.block.main-menu ul.menu li.first a:hover {
    background: url(/static/img/domik_active.png);
    background-repeat: no-repeat;
    width: 26px;
    font-size: 0;
}




#block-simplesearch-simplesearch {
    display: block;
    float: right;
    margin: 15px 0 0 0;
    padding: 0;
}
#block-simplesearch-simplesearch div.content div.form-item-search {
    display: block;
    float: left;
    margin: 0;
    padding: 0;
    background: url(/static/img/search-area.png) no-repeat scroll 0 0 rgba(0,0,0,0);
}
.block-simplesearch {
    z-index: 100;
}
#block-simplesearch-simplesearch div.content input.form-text {
    width: 194px;
    height: 28px;
    margin: 0;
    padding: 1px 10px 0 10px;
    border: 0;
    background-color: transparent;
    font: italic 12px Arial;
    color: #333;
    line-height: 28px;
}

.header-phone {
    float: right;
    right: 50px;
    height: 48px;
    margin-top: 10px;
}
.header-phone-block {
    font-family: 'ErasBoldITCRegular';
    padding-top: 10px;
}
.header-phone-block .header-phone-block-code {
    font-size: 13.5pt;
    color: #cac9c8;
}
.header-phone-block .header-phone-block-number {
    
    font-size: 15.75pt;
    color: #fff;
    margin-left: 5px;
}
.header-mail-block .header-mail-block-code {
    font-size: 9.5pt;
    color: #cac9c8;
}
.header-mail-block .header-mail-block-number {
    cursor: pointer;
    font-size: 9.75pt;
    color: #fff;
    margin-left: 5px;
}

























